/* Centrar el contenido en toda la pantalla */
body, html {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5; /* Fondo suave */
}

/* Estilo general de la tabla */
.pulidora-table {
  display: flex;
  justify-content: center; /* Centra las tablas horizontalmente */
  flex-wrap: wrap;
  gap: 30px;
  width: 100%;
  max-width: 1200px;
  overflow: hidden; /* Evitar el scroll horizontal */
}

/* Estilo de las tablas */
.pulidora-table table {
  width: 100%;
  max-width: 600px; /* Limita el ancho de la tabla */
  border-collapse: collapse;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  table-layout: auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

/* Efecto hover sobre las tablas */
.pulidora-table table:hover {
  transform: translateY(-5px);
}

/* Estilos de celdas y encabezados */
table th, table td {
  padding: 15px;
  border: 1px solid #ddd;
  text-align: center;
  word-wrap: break-word; /* Ajusta el contenido en las celdas */
  max-width: 200px; /* Evitar que se expandan demasiado */
}

/* Estilo de encabezados */
table th {
  background-color: #58abcc;
  color: white;
  text-transform: uppercase;
}

/* Colores condicionales */
.verde { background-color: #90EE90; } /* Verde */
.rojo { background-color: #FF7F7F; } /* Rojo */
.naranja { background-color: #FFB84D; } /* Naranja */

/* Estilos responsivos */
@media (max-width: 768px) {
  .pulidora-table {
    flex-direction: column;
    align-items: center;
  }

  .pulidora-table table {
    width: 90%; /* Ajustar el ancho en móviles */
  }

  h1 {
    text-align: center;
    width: 100%;
    margin-top: 20px; /* Ajusta el margen superior si es necesario */
    color: #333; /* Color del texto */
  }

  table th, table td {
    font-size: 14px; /* Reducir tamaño de fuente en pantallas pequeñas */
    padding: 8px;
  }
}

/* Bordes personalizados */
.tabla-pulidores th:nth-child(5), .tabla-pulidores td:nth-child(5) {
  border-right: 50px solid #f5ebeb;
}

.tabla-pulidores th:nth-child(6), .tabla-pulidores td:nth-child(6) {
  border-left: 50px solid #f5ebeb;
}
